@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";


#root {
    max-width: 1200px;
    margin: auto;
}

#good-image {
    width: 250px;
}

#content {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: center;
    padding: 20px;
}

#gallery {
    display: flex;
    justify-content: center;
}

/* style={{ display: 'flex', justifyContent: 'center'}} */
/* style={{padding: '20px', display: 'flex', flexWrap: 'wrap', gap: '40px', justifyContent: 'center'}}> */

@media (min-width: 1200px) {
    #good-image {
        width: 400px;
    }

    #gallery {
        max-width: 600px;
    }

    #bottom-call-button {
        display: none;
    }
}